var skill_map = {
    'java': ['java message service (jms)', 'user interface design'],
    'liferay': ['java message service (jms)', 'user interface design']
};

export function skillMap() {
    return skill_map;
}

export function skillSet() {
    return _.difference(skill_set, extractSkillsFromSkillMap()).sort();
}

export function createNewSynonymBlock(skill) {
    skill_map = {...skill_map, ...{[skill] : []}}
}

export function removeSynonym(skill, synonym) {
    const synonyms = skill_map[skill];
    skill_map = {...skill_map, ...{[skill] : synonyms.filter( s => s !== synonym)}}
}

export function addNewSynonym(skill, synonym) {
    const synonyms = skill_map[skill];
    skill_map = {...skill_map, ...{[skill] : [...synonyms, synonym]}}
}

export function extractSkillsFromSkillMap() {
    const keys = Object.keys(skill_map);
    const values = Object.values(skill_map);
    return [...keys, ..._.flatten(values)];
}
